import User from './User';

export default class Admin extends User {
  constructor(user, selector) {
    super(user, selector);
    this.addtionalRights = ['add', 'remove', 'updatedd'];

    this.init();
  }

  init() {
    super.init();
    this.extendUserRights();
  }

  extendUserRights() {
    this.addtionalRights.addUserRights.forEach(right => this.user.rights.push(right));
  }
}
